import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {AuthenticationProvider} from "../../providers/authentication/authentication";
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";


@IonicPage()
@Component({
  selector: 'page-change-password01',
  templateUrl: 'change-password01.html',
})
export class ChangePassword01Page {

  passPolicy;
  modifyOldPassError = "";
  passWd = {oldPassWd: "", newPassWd: "",confirmPassWd: ""};

  oldPass = "";
  newPass = "";
  confirmPass = "";

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public maskTokenUtil: MaskTokenProvider,
              public storage: LocalStorageProvider,
              public alertCtrl: AlertController,
              public authenticationService: AuthenticationProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public tokenService : TokenServiceProvider,
              public gaProvider : GoogleAnalyticsProvider) {

    this.passPolicy = this.storage.retrieveFromStorage("passPolicy");
  }

  ChangePassword01Action = function () {

    this.modifyOldPassError = "";
    this.oldPass = this.passWd.oldPassWd;
    this.newPass =this.passWd.newPassWd;
    this.confirmPass = this.passWd.confirmPassWd;

    let flag = true;

    if (this.oldPass == "" || typeof(this.oldPass) == "undefined")
    {
      this.modifyOldPassError = "<li>Please Enter Old Password."
      flag = false;
      return;
    }

    if (this.newPass == "" || typeof(this.newPass) == "undefined")
    {
      this.modifyOldPassError = "<li>Please Enter New Password."
      flag = false;
      return;
    }

    if (this.confirmPass == "" || typeof(this.confirmPass) == "undefined")
    {
      this.modifyOldPassError = "<li>Please Enter Confirm Password."
      flag = false;
      return;

    }

    if (!(this.newPass == this.confirmPass))
    {
      this.modifyOldPassError = "<li>Password and Confirm Password Mismatch."
      flag = false;
      this.passWd = {oldPassWd: "", newPassWd: "",confirmPassWd: ""};
      return;
    }

    if (flag) {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
      this.tokenService.getNewToken(unMaskRefToken)
        .then(data => {
          this.onNewTokenGenComplete(data);
        });
    }
  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let encOldPass = this.cryptoUtil.getEncryptVal(this.oldPass);
      let encNewPass = this.cryptoUtil.getEncryptVal(this.newPass);
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      this.authenticationService.modifyOldPassWd(unMaskToken, appData.userId, appData.appId, encOldPass, encNewPass)
        .then(data => {
          this.onChangePasswordComplete(data);
        });
    }
  };

  onChangePasswordComplete = function (data) {
    if (data.statusCode == 1) {
      this.navCtrl.push('ChangePassword02Page', {});
    }

    else if (data.statusCode == 0) {
      this.modifyOldPassError = data.note;
      this.passWd = {oldPassWd: "", newPassWd: "",confirmPassWd: ""};
    }

    else {
      this.modifyOldPassError = "System Error Contact with Administrator.";
    }
  };

  showPasswordPolicy= function () {
    let radioAlert = this.alertCtrl.create();
    radioAlert.setTitle('Password Policy');
    radioAlert.setCssClass("alertLogCss");
    radioAlert.setMessage(this.passPolicy);
    radioAlert.addButton({
      text: 'OK',
      handler: data => {

      }
    });
    radioAlert.present();
    setTimeout(() => {
      radioAlert.dismiss();
    }, 30000);
  };


  ionViewDidLoad() {
    // console.log('ionViewDidLoad ChangePassword01Page');
    this.gaProvider.startTrackerWithId("Change Password");
  }

}



